export default {
	data() {
		return {
			isActive: 1,
			buy: false,
			checkedRadio: false,
			buyRadio: false
		}
	},
	methods: {
		tab1(val){
			this.isActive = val
		},
		radioChange(evt){
			this.checkedRadio = !this.checkedRadio
			this.buy = !this.buy
		},
		radioChanges(){
			this.buyRadio = !this.buyRadio
		}	
	},
	onShow() {
		uni.setTabBarItem({
			index: 0,
			text: '首页',
			"iconPath": "static/img/hidden_index2.png",
