		 "iconPath": "static/img/hidden_case1.png",
		  selectedIconPath: 'static/img/hidden_case1.png'
		})
		uni.setTabBarItem({
		  index: 2,
		  text: '我的',
		 "iconPath": "static/img/hidden_user2.png",
		  selectedIconPath: 'static/img/hidden_user2.png'
		})
		uni.setTabBarStyle({
